.heroHome {
  width: 100%;
  max-width: 1520px;
  margin: 0 auto;
  position: relative;
  overflow: hidden;
}
.heroHome::after {
  content: '';
  position: absolute;
  left: 64%;
  top: -41px;
  width: 555px;
  height: 599px;
  background-image: url('../../icons/galaxy-empty.svg');
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  z-index: 1;
}
.heroHome::before {
  content: '';
  position: absolute;
  left: 64%;
  top: -32px;
  width: 555px;
  height: 599px;
  background-image: url('../../icons/galaxy-stars.svg');
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  z-index: 0;
  animation: rotateStars 60s linear infinite;
}

@keyframes rotateStars {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

@media screen and (max-width: 768px) {
  .heroHome::after {
    content: '';
    position: absolute;
    right: -62px;
    bottom: 36px;
    top: unset;
    left: unset;
    width: 373px;
    height: 332px;
    background-image: url('../../icons/galaxy-empty.svg');
    background-position: center;
    background-repeat: no-repeat;
    background-size: contain;
    z-index: -1;
  }
}

@media screen and (max-width: 768px) {
  .heroHome::before {
    content: '';
    position: absolute;
    right: -62px;
    bottom: 36px;
    top: unset;
    left: unset;
    width: 373px;
    height: 332px;
    background-image: url('../../icons/galaxy-stars.svg');
    background-position: center;
    background-repeat: no-repeat;
    background-size: contain;
    z-index: -1;
  }
}

.heroContent {
  max-width: 1180px;
  padding: 90px 40px 140px 40px;
  width: 100%;
  display: block;
  margin: 0 auto;
  position: relative;
  z-index: 3;
}
@media screen and (max-width: 768px) {
  .heroContent {
    padding: 0px;
  }
}

@media screen and (max-width: 768px) {
  .heroHome {
    padding: 25px 20px 400px 20px;
  }
}
.heroWrap {
  max-width: 586px;
  margin-right: auto;
}
.heroTitle {
  color: #161616;
  font-family: 'TWK700';
  font-size: 60px;
  font-style: normal;
  line-height: 1.2;
  margin-bottom: 25px;
}

@media screen and (max-width: 768px) {
  .heroTitle {
    font-size: 35px;
  }
}

.heroTitle span {
  color: #f948b4;
}

.heroSubTitle {
  color: #f948b4;
}
.heroText {
  color: #161616;
  font-size: 18px;
  font-style: normal;
  font-weight: 400;
  line-height: 1.2;
  margin-bottom: 25px;
}
.heroLink {
  position: relative;
  display: inline-block;
  overflow: hidden;
  text-decoration: none;
  padding: 20px 30px;
  color: #fff;
  font-family: 'TWK700';
  font-size: 25px;
  line-height: 1.25;
  border-radius: 12px;
  background: #f948b4;
  box-shadow: 0px 2px 3px 0px rgba(0, 0, 0, 0.15);
}

.linkText {
  z-index: 1;
  color: #fff;
  font-family: 'TWK700';
  font-size: 25px;
  font-weight: 700;
  line-height: 1.25;
}

.decor {
  display: block;
  height: 100%;
  left: 0;
  position: absolute;
  top: 0;
  width: 100%;
}

.decorItem {
  background: rgba(255, 255, 255, 0.1);
  border-radius: 12px;
  transform: translate(-100%);
  will-change: transform;
  display: block;
  height: 100%;
  left: 0;
  position: absolute;
  top: 0;
  width: 100%;
}
